.navbar{
    background-color: #161b18 !important;
}

.nav__logo{
    width: 110px;
}

.link_name{
    padding: 10px 15px;
    color: #ffffff;
    transition: transform .2s;
}

.link_name:hover{
    color: #bf7504;
    transform: scale(1.1);
}

@media screen and (min-width:480px){
    .nav__logo{
        margin-left: 10px;
    }
}

@media screen and (min-width:768px){
    .nav__logo{
        margin-left: 40px;
    }
}

@media screen and (min-width:1024px){
    .nav__logo{
        margin-right: 550px;
    }
}